.contact-page {
    display: flex;
    flex-direction: column;
    form {
        align-self: center;
        input {
            margin: 0;
        }
    }
    .add-contact-btn {
        color: whitesmoke;
        font-size: 1rem;
        background-color: $clr3;
        &:hover {
            background-color: darken($color: $clr3, $amount: 50);
        }
        align-self: center;
        text-decoration: none;
        border-radius: 50%;
        border: none;
        a {
            text-decoration: none;
        }
    }
}