.moves-list {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    border-radius: 0.25rem;
    padding: 0;
    display: flex;
    flex-direction: column;
    background-color: $clr2;
    color: whitesmoke;
    .moves-list-ul {
        flex-direction: column;
    }
    h3 {
        margin: 0;
        border-bottom: 0.1rem solid #f0e0e0;
    }
    .move {
        padding: 1rem;
        background-color: $clr2;
        border-bottom: 0.1rem solid #f0e0e0;
    }
}